import { Container } from "@mui/system";
import React, { useRef, useState } from "react";
import "./App.css";

const App = () => {
  const [result, setResult] = useState(0);
  const [input, setInput] = useState("");
  // const [history, setHistory] = useState([]);

  // const arrayRef = useRef([]);
  // const history = [];
  // let temp = '';
  

  const handleClick = (button) => {
    if (button === "C") {
      setResult(0);
      setInput("");
    } else if (button === "=") {
      setResult(eval(input));
      // setInput("");
    } else {
      setInput(input + button);
      
    }
  };

  // history = arrayRef;

  // console.log(input);
  // console.log(arrayRef[0]);
  // console.log(temp);

  return (
    <div className="App" style={{ height: "100vh" }}>
      <Container
        maxWidth="md"
        style={{
          display: "flex",
          padding: "1px",
          // flexDirection: "column",
          height: "100vh",
          // backgroundColor: "red",
        }}
      >
        <div className="container-cal">
          <div className="text-area">
            <div className="text-box-area">
              {/* <h1>{input}</h1> */}
            <input type="text" value={input} disabled/>
            {/* <p>{temp}</p> */}
            </div>
            <p>{result}</p>
          </div>
          <div className="buttons-all">
            <button onClick={() => handleClick("1")}>1</button>
            <button onClick={() => handleClick("2")}>2</button>
            <button onClick={() => handleClick("3")}>3</button>
            <button className="operator" onClick={() => handleClick("+")}>+</button>
            <br />
            <button onClick={() => handleClick("4")}>4</button>
            <button onClick={() => handleClick("5")}>5</button>
            <button onClick={() => handleClick("6")}>6</button>
            <button className="operator" onClick={() => handleClick("-")}>-</button>
            <br />
            <button onClick={() => handleClick("7")}>7</button>
            <button onClick={() => handleClick("8")}>8</button>
            <button onClick={() => handleClick("9")}>9</button>
            <button className="operator" onClick={() => handleClick("*")}>x</button>
            <br />
            <button className="clear1" onClick={() => handleClick("C")}>C</button>
            <button onClick={() => handleClick("0")}>0</button>
            <button className="equal" onClick={() => handleClick("=")}>=</button>
            <button className="operator" onClick={() => handleClick("/")}></button>
          </div>
        </div>
      </Container>
    </div>
  );
};

export default App;
